import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { motion, AnimatePresence } from 'framer-motion';
import { CProgress, CProgressBar } from '@coreui/react';
import { Ether, Hardhat, shortenTransactionHash, useBlockNumber } from '@usedapp/core';
import { Link } from 'react-router-dom';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { utils } from 'ethers';
import CustomDialog from '../../Dialog/CustomDialog';

import ClaimButton from '../../Form/ClaimButton';

import { useEthers, shortenAddress, useContractFunction, useContractCalls, useEtherBalance } from '@usedapp/core';
import { XITRAM_SEED_ADDRESS, XITRAM_SEED_CONTRACT, XITRAM_SEED_INTERFACE, USDC_ADDRESS, USDC_CONTRACT, ETH_DECIMALS } from '../../../config';
import { fCRCurrency, fCRCurrency8 } from '../../../utils/formatNumber';
import ReactPlaceholder from 'react-placeholder';
import "react-placeholder/lib/reactPlaceholder.css"
import dateFormat from "dateformat";
import { XIRTAM_DATA } from './constants';
import StatusPill from '../../Elements/StatusPill';
import TimeLabel from '../../Elements/TimeLabel';
import TelegramBox from '../../Elements/TelegramBox';

const { price, price_currency, name, symbol, actual_price_currency, logo } = XIRTAM_DATA;

export default function XirtamPage() {
  const { account } = useEthers();

  // SUCCESS DIALOG
  const [successModalOpen, setSuccessModalOpen] = useState(false);
  const [successClaimModalOpen, setSuccessClaimModalOpen] = useState(false);

  const handleCloseSuccessModal = () => {
    setSuccessModalOpen(false);
  };

  const handleCloseSuccessClaimModal = () => {
    setSuccessClaimModalOpen(false);
  }

  // SALE
  const [saleProgress, setSaleProgress] = useState(0);
  const [getAmount, setGetAmount] = useState("0");

  const [amount, setAmount] = useState("0.1")

  const [startTime, setStartTime] = useState(0)
  const [endTime, setEndTime] = useState(0)
  const [status, setStatus] = useState(0)
  const [statusText, setStatusText] = useState('')
  const [softCap, setSoftCap] = useState(utils.parseEther('0'))
  const [hardCap, setHardCap] = useState(utils.parseEther('0'))
  const [minBuy, setMinBuy] = useState(utils.parseEther('0'))
  const [maxBuy, setMaxBuy] = useState(utils.parseEther('0'))
  const [totalCollected, setTotalCollected] = useState(utils.parseEther('0'))
  const [totalTokens, setTotalTokens] = useState(utils.parseEther('0'))
  const [claimTokenBalance, setClaimTokenBalance] = useState(utils.parseEther('0'))

  const [buyPrice, setBuyPrice] = useState(utils.parseEther('0'))

  const [isTx, setIsTx] = useState(false)
  const [tx, setTx] = useState('')

  const contractData = useContractCalls([
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'startTime',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'endTime',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'status',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'SOFTCAP',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'HARDCAP',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'MIN_ETH_PER_WALLET',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'MAX_ETH_PER_WALLET',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'totalCollected',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'participants',
      args: [account]
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'getLatestPrice',
      args: []
    },
    {
      abi: XITRAM_SEED_INTERFACE,
      address: XITRAM_SEED_ADDRESS,
      method: 'getTokenAmount',
      args: [account]
    }
  ]);


  const ethBalance = useEtherBalance(account)

  const [errorMessage, setErrorMessage] = useState(0)

  useEffect(() => {
    if (contractData[0]) {

      var date = new Date(contractData[0].toString() * 1000);

      setStartTime(dateFormat(date, 'mmmm dS, yyyy, HH:MM:ss'))
    }

    if (contractData[1]) {
      var date = new Date(contractData[1].toString() * 1000);

      setEndTime(dateFormat(date, 'mmmm dS, yyyy, HH:MM:ss'))
    }

    if (contractData[2]) {
      setStatus(contractData[2]);

      if (contractData[2] == 0) {
        setStatusText('Scheduled')
      }

      if (contractData[2] == 1) {
        setStatusText('Active')
      }

      if (contractData[2] == 2) {
        setStatusText('Closed')
      }

      if (contractData[2] == 3) {
        setStatusText('Failed')
      }
    }

    if (contractData[3]) {
      setSoftCap(contractData[3][0]);
    }

    if (contractData[4]) {
      setHardCap(contractData[4][0]);
    }

    if (contractData[5]) {
      setMinBuy(contractData[5][0]);
    }

    if (contractData[6]) {
      setMaxBuy(contractData[6][0]);
    }

    if (contractData[7]) {
      setTotalCollected(contractData[7][0]);
    }

    if (contractData[8]) {
      setTotalTokens(contractData[8][0]);
    }


    if (contractData[9]) {
      setBuyPrice(contractData[9][0]);
    }

    if (contractData[10]) {
      setClaimTokenBalance(contractData[10][0]);
    }


    if (contractData[4] && contractData[7]) {

      const progress = contractData[7][0].mul(utils.parseUnits('100', 6)).div(contractData[4][0])

      setSaleProgress(Number(utils.formatUnits(progress, 6)))
    }

  }, [contractData]);



  useEffect(() => {

    if (isTx) {
      setSuccessModalOpen(true);
      setIsTx(false)
    }
  }, [totalTokens, tx]);

  const {
    register,
    handleSubmit,
    setValue,
    value,
    setError,
    watch,
    formState: { errors }
  } = useForm({
    defaultValues: {
      amount: "0.1"
    }
  });
  const [result, setResult] = useState('');
  const watchFields = watch(['amount']);

  useEffect(() => {
    const subscription = watch((value, { name, type }) => {
      const decimals = value.amount.split(".")

      if (value.amount == '') {
        setAmount('0')
      } else {
        setAmount(value.amount)
      }

      if (decimals.length > 1 && decimals[1].length > 18) {
        setAmount('0')
      } else {
        if (value.amount != "") {
          const priceInUSD = utils.parseEther(value.amount.toString()).mul(buyPrice).div(utils.parseUnits("1", 8))
          const price = utils.parseEther("0.03")

          const xtr = utils.formatEther(priceInUSD.mul(utils.parseEther("1").div(price)))
          setGetAmount(xtr);
        }
      }
    });

    setErrorMessage('')
    return () => subscription.unsubscribe();
  }, [watchFields]);


  console.log(amount)
  useEffect(() => {
    let cAmount;
    if (amount == "0") {
      cAmount = "0.01"
    } else {
      cAmount = amount
    }
    const priceInUSD = utils.parseEther(cAmount.toString()).mul(buyPrice).div(utils.parseUnits("1", 8))
    const price = utils.parseEther("0.03")

    const xtr = utils.formatEther(priceInUSD.mul(utils.parseEther("1").div(price)))
    setGetAmount(xtr);

    const amountBN = utils.parseUnits(amount.toString(), ETH_DECIMALS)

  }, [minBuy, maxBuy]);

  const handleMaxBuy = (e) => {
    setValue('amount', utils.formatUnits((maxBuy.sub(totalTokens)).toString(), ETH_DECIMALS).toString(), {
      shouldValidate: true,
      shouldDirty: true
    });
    e.preventDefault();
  };

  const { state, send } = useContractFunction(XITRAM_SEED_CONTRACT, 'sell', { transactionName: 'Sell Tokens' });

  const [buying, setBuying] = useState(false)

  useEffect(() => {

    if (state.status == 'Exception') {
      setErrorMessage(state.errorMessage)
    }
    if (state.status === 'Exception' || state.status === 'Fail') {
      setBuying(false)
      setIsTx(false)
      setErrorMessage(state.errorMessage)
    }

    if (state.status === 'None') {
      setBuying(false)
      setIsTx(false)
      setErrorMessage('')
    }

    if (state.status === 'Success') {
      setBuying(false)
      setTx(state.transaction.hash)
      setIsTx(true)
      setErrorMessage('')
    }
  }, [state]);

  const handleBuySubmit = (data) => {
    setBuying(true)
    send({ value: utils.parseUnits(amount.toString(), ETH_DECIMALS).toString() })
  };

  const onError = (errors, e) => console.log(errors, e);

  // TODO: make it to false later
  const [accessState, setAccessState] = useState(true);
  const [passwordState, setPasswordState] = useState('');

  const handleAccess = async () => {
    var requestOptions = {
      method: 'GET',
      redirect: 'follow'
    };

    fetch(`https://admin.pincubator9059.workers.dev/?passwd=${passwordState}`, requestOptions)
      .then(response => response.text())
      .then(result => {
        if (result === 'VALID') {
          setAccessState(true);
        } else {
          alert("INVALID PASSWORD");
        }
      })
      .catch(error => console.log('error', error));
  }

  const handlePasswordChange = (e) => {
    setPasswordState(e.target.value);
  }

  // copy to clipboard
  const [copied, setCopied] = useState(false);
  // set copied to false if copied is true after 2 seconds
  useEffect(() => {
    if (copied) {
      const timeout = setTimeout(() => setCopied(false), 2000);
      return () => clearTimeout(timeout);
    }
  }, [copied]);

  return (
    <>
      {!accessState &&
        <section className="d-flex flex-column justify-content-center align-items-center" style={{ height: '100vh', width: '100vw' }}>
          <h3 className="text-center">Input Public Round Password for</h3>
          <h2 style={{ fontWeight: 'bold' }}>${symbol}</h2>

          <input className="purple-input form-control" style={{ maxWidth: '30rem' }} placeholder="Password" value={passwordState} onChange={handlePasswordChange} />
          <button onClick={handleAccess} className="button-outline mt-3">ACCESS</button>
        </section>
      }

      {accessState &&
        <>
          <section className="section-dashboard">
            <motion.div
              initial={{ opacity: 0, y: '-20vh' }}
              animate={{ opacity: 1, y: '0' }}
              exit={{ opacity: 0 }}
              transition={{ duration: 1, type: 'spring' }}
              className="container pt-5 d-flex flex-column"
              style={{ height: '100%', minHeight: '90vh', paddingTop: '7rem' }}
            >
              <Link to="/" className="mt-5 mb-3">
                <button className="button-outline">Go Back</button>
              </Link>

              <div className="Seed-introduction mb-5">
                <div className='row'>
                  <div className='col-12 col-lg-8 mb-4'>
                    <h4 style={{ fontWeight: '600' }}>
                      Token Info
                    </h4>

                    <div className='gradient-border p-3 d-flex flex-column mb-3'>
                      <div className='d-flex flex-row align-items-center w-100'>
                        <img src={logo} alt="" className="Seed-introduction-logo" />

                        <div className='pl-4 flex flex-column w-100'>
                          <div className='d-flex flex-row items-center' style={{ justifyContent: 'space-between' }}>
                            <h4>
                              {name} (${symbol})
                            </h4>

                            <StatusPill status={statusText} />
                          </div>

                          <div className='d-flex flex-row items-center' style={{ justifyContent: 'space-between' }}>
                            <h6 style={{ fontWeight: '400' }}>
                              {/* 1 {price_currency} = {1 / price} {symbol} */}
                              1 {symbol} = {1 / price} {price_currency}
                            </h6>

                            <div>
                              Network: Ethereum
                            </div>
                          </div>
                        </div>
                      </div>

                      <p style={{ fontSize: '0.8rem', marginTop: '1rem' }}>
                        XIRTAM gives players a totally original and enjoyable experience to utilize their expertise to make money.
                        <br /><br />
                        Players are emerged into a world of FREE play-to-earn gaming offering choice and excitement in their gameplay.
                        <br /><br />
                        XIRTAM is a non-KYC reputation-building platform created for and by the community to promote both cryptocurrency education and financial incentives for all users.
                        <br /><br />
                        Partnered with $CULT, $VOLT and an integrated utility for the $TSUKA ecosystem, XIRTAM is leading the #GameFi industry for 2023
                      </p>

                      <p style={{ fontSize: '0.8rem', marginTop: '1rem' }}>
                        Website: <a target='_blank' rel='noreferrer' href="https://www.xirtamtoken.com">https://www.xirtamtoken.com</a>
                        <br /><br />
                        Whitepaper: <a target='_blank' rel='noreferrer' href="https://xirtam.gitbook.io/docs/">https://xirtam.gitbook.io/docs/</a>
                        <br /><br />
                        Twitter: <a target='_blank' rel='noreferrer' href="https://twitter.com/xirtamtoken">https://twitter.com/xirtamtoken</a>
                        <br /><br />
                        Telegram: <a target='_blank' rel='noreferrer' href="https://t.me/xirtamtoken">https://t.me/xirtamtoken</a>
                        <br /><br />
                        Medium: <a target='_blank' rel='noreferrer' href="https://medium.com/@xirtamtoken">https://medium.com/@xirtamtoken</a>
                      </p>
                    </div>

                    {/* SALE BOX */}
                    {(status == 1 && account && (maxBuy.sub(totalTokens).toString()) != 0) &&
                      <div className="mb-3">
                        <h4 className="fw-bold">Sale</h4>
                        <div className="d-flex flex-column round-purple-border mt-2 bg-darken p-2">
                          <form onSubmit={handleSubmit(handleBuySubmit, onError)}>
                            <div className="d-flex flex-row mt-2">
                              <input
                                {...register('amount', { required: true, max: utils.formatUnits((maxBuy.sub(totalTokens)).toString(), ETH_DECIMALS), min: utils.formatUnits(minBuy.toString(), ETH_DECIMALS) })}
                                type="number"
                                step="0.000000000000000001"
                                className="form-control purple-input Seed-buy-amount"
                                placeholder="Buy amount"
                              />
                              <button onClick={handleMaxBuy} className="button-outline purple-fill Seed-max-btn">
                                MAX
                              </button>
                            </div>
                            {errors.amount && (
                              <span className="invalid-danger" style={{ marginTop: '-0.2' }}>
                                Min: {utils.formatUnits(minBuy.toString(), ETH_DECIMALS)} {price_currency} | Max: {utils.formatUnits((maxBuy.sub(totalTokens)).toString(), ETH_DECIMALS)} {price_currency}
                              </span>
                            )}

                            {errorMessage ? (
                              <span className="invalid-danger" style={{ marginTop: '-0.2' }}>
                                {errorMessage}
                              </span>
                            ) : (
                              ''
                            )}
                            {<div className="buy-result">You'll get ~ {fCRCurrency8(getAmount)} {symbol}</div>}

                            {(ethBalance !== undefined && ethBalance.gte(utils.parseUnits(amount))) ? (
                              <button className="button-outline purple-fill mt-4 w-100" style={{ padding: '0.3rem' }} type="submit" disabled={buying}>
                                {!buying ? (
                                  'Buy'
                                ) : (
                                  'Buying...'
                                )}
                              </button>
                            ) : (
                              <span className="invalid-danger" style={{ marginTop: '-0.2' }}>
                                No {actual_price_currency} available to buy. {ethBalance != undefined ? "Available " + fCRCurrency8(utils.formatEther(ethBalance)) : ""}
                              </span>
                            )}
                          </form>

                          {(account && ((maxBuy.sub(totalTokens).toString()) == 0)) ? (
                            <div>
                              <span className="invalid-danger" style={{ marginTop: '-0.2' }}>
                                Max ETH per wallet has been reached
                              </span>
                            </div>
                          ) : (
                            ''
                          )}
                        </div>
                      </div>
                    }

                    {account &&
                      <>
                        <div className="mb-3">
                          <h5>My Balance Info</h5>

                          <div className="Seed-sale-item gradient-border">
                            <h6 className="m-1">
                              XITRAM Balance: <span className="Seed-sale-item-amount">
                                {claimTokenBalance ? (
                                  <ReactPlaceholder type="textRow" ready={claimTokenBalance !== undefined} showLoadingAnimation={true}>
                                    {fCRCurrency8(utils.formatEther((claimTokenBalance)))} XIRTAM
                                  </ReactPlaceholder>
                                ) : (
                                  '0 VINCI'
                                )}

                              </span>
                            </h6>
                          </div>
                        </div>

                        {/* Address Box */}
                        <div className='gradient-border p-3 flex-column mb-3'>
                          <div style={{ fontSize: '0.8rem' }}>
                            Alternatively, you can buy XIRTAM by sending ETH directly to the following address:
                          </div>
                          <div className='d-flex flex-row align-items-center'>
                            <a
                              // link to etherscan
                              target='_blank'
                              rel='noreferrer'
                              href={`https://etherscan.io/address/${XITRAM_SEED_ADDRESS}`}
                              style={{ fontWeight: '600', marginTop: '0.2rem', marginBottom: '0', fontSize: '1rem' }}>
                              {XITRAM_SEED_ADDRESS}
                            </a>

                            <CopyToClipboard text={XITRAM_SEED_ADDRESS} onCopy={() => setCopied(true)}>
                              <button className='btn-hover purple-fill m-0 ms-auto py-1 px-2' style={{ fontSize: '0.8rem' }}>
                                {copied ? 'Copied' : 'Copy'}
                              </button>
                            </CopyToClipboard>
                          </div>
                        </div>
                      </>
                    }
                  </div>


                  {/* SALE INFO */}
                  <div className="col-12 col-lg-4">
                    <h4 style={{ fontWeight: '600' }}>
                      Sale Info
                    </h4>

                    <div className='mb-3'>
                      <div className="d-flex flex-row Seed-progress-label" style={{ width: '100%' }}>
                        <h6 style={{ fontSize: '0.8rem' }}>
                          Sale Progress
                        </h6>
                        <div className="progress-percent-container ms-auto">
                          {saleProgress.toFixed(2)}%
                        </div>
                      </div>
                      <CProgress className="mb-1">
                        <CProgressBar color="#CCCCCC" variant="striped" animated value={saleProgress} />
                      </CProgress>

                      <div style={{ fontSize: '0.8rem', textAlign: 'right' }}>
                        <TimeLabel startTime={startTime} endTime={endTime} status={statusText} isUnix={false} />
                      </div>
                    </div>

                    <div className="gradient-border p-2 mb-3">
                      <h6 className="m-1" style={{ lineHeight: '1.5' }}>
                        <ReactPlaceholder type="textRow" ready={true} showLoadingAnimation={true}>
                          {/* <ReactPlaceholder type="textRow" ready={hardCap.toString() > 0} showLoadingAnimation={true}> */}
                          Hardcap: <span className="Seed-sale-item-amount">{fCRCurrency(utils.formatUnits(hardCap.toString(), ETH_DECIMALS))} {actual_price_currency}</span>
                        </ReactPlaceholder>
                      </h6>
                    </div>

                    <div className="gradient-border p-2 mb-3">
                      <h6 className="m-1" style={{ lineHeight: '1.5' }}>
                        <ReactPlaceholder type="textRow" ready={true} showLoadingAnimation={true}>
                          {/* <ReactPlaceholder type="textRow" ready={minBuy.toString() > 0} showLoadingAnimation={true}> */}
                          Min {actual_price_currency} per wallet: <span className="Seed-sale-item-amount d-block">{fCRCurrency(utils.formatUnits(minBuy.toString(), ETH_DECIMALS))} {actual_price_currency}</span>
                        </ReactPlaceholder>
                      </h6>
                    </div>

                    <div className="gradient-border p-2 mb-3">
                      <h6 className="m-1" style={{ lineHeight: '1.5' }}>
                        <ReactPlaceholder type="textRow" ready={true} showLoadingAnimation={true}>
                          {/* <ReactPlaceholder type="textRow" ready={maxBuy.toString() > 0} showLoadingAnimation={true}> */}
                          Max {actual_price_currency} per wallet: <span className="Seed-sale-item-amount d-block">{fCRCurrency(utils.formatUnits(maxBuy.toString(), ETH_DECIMALS))} {actual_price_currency}</span>
                        </ReactPlaceholder>
                      </h6>
                    </div>

                    <div className="gradient-border p-2 mb-3">
                      <h6 className="m-1" style={{ lineHeight: '1.5' }}>
                        <ReactPlaceholder type="textRow" ready={true} showLoadingAnimation={true}>
                          {/* <ReactPlaceholder type="textRow" ready={totalCollected.toString()} showLoadingAnimation={true}> */}
                          Total Collected: <span className="Seed-sale-item-amount">{fCRCurrency8(utils.formatUnits(totalCollected.toString(), ETH_DECIMALS))} {actual_price_currency}</span>
                        </ReactPlaceholder>
                      </h6>
                    </div>

                    <div className="gradient-border p-2 mb-3">
                      <h6 className="m-1" style={{ lineHeight: '1.5' }}>
                        <ReactPlaceholder type="textRow" ready={true} showLoadingAnimation={true}>
                          {/* <ReactPlaceholder type="textRow" ready={maxBuy.toString() > 0} showLoadingAnimation={true}> */}
                          Current {actual_price_currency} / USD rate: <span className="Seed-sale-item-amount d-block">{utils.formatUnits(buyPrice.toString(), 8)} $</span>
                        </ReactPlaceholder>
                      </h6>
                    </div>

                    <TelegramBox />
                  </div>
                </div>
              </div>
            </motion.div>
          </section>

          <AnimatePresence initial={false} exitBeforeEnter={true} onExitComplete={() => null}>
            {successModalOpen && (
              <CustomDialog modalOpen={successModalOpen} handleClose={handleCloseSuccessModal}>
                <img src="/static/img/success-check.svg" alt="" style={{ width: '3rem', marginBottom: '1rem' }} />

                <h2 className="text-center">Buy Success!</h2>
                <h6 className="text-center">

                  {tx ? (
                    <>
                      Tx:{' '}
                      <a
                        href={`https://sepolia.etherscan.io/tx/${tx}`}
                        target="_blank"
                      >
                        {shortenTransactionHash(tx)}
                      </a>
                    </>
                  ) : (
                    ''
                  )}
                </h6>

                <div className="d-flex flex-wrap flex-row justify-content-center mt-3">
                  <button className="button-outline m-2 purple-fill" onClick={handleCloseSuccessModal}>
                    Alright!
                  </button>
                </div>
              </CustomDialog>
            )}
          </AnimatePresence>
        </>
      }
    </>
  );
}

