import React from 'react'

export default function StatusPill({ status, formattedStartDate, formattedEndDate }) {
  let withTime = false;
  if (formattedStartDate && formattedEndDate) withTime = true;

  let textColor = 'white'
  if(status === "Active"){
    textColor = 'black'
  }

  return (
    <>
      {status === "Scheduled" &&
        <div>
          <div className="pill" style={{ backgroundColor: 'var(--main-purple)', color: textColor }}>
            Scheduled
          </div>
          {withTime &&
            <div style={{ fontSize: '0.8rem', marginTop: '1rem' }}>
              <span style={{ opacity: '0.6' }}>
                Start at:&nbsp;
              </span>
              <span>
                {formattedStartDate}
              </span>
            </div>
          }
        </div>
      }

      {status === "Active" &&
        <div>
          <div className="pill" style={{ backgroundColor: 'var(--main-green)', color: textColor }}>
            Active
          </div>
          {withTime &&
            <div style={{ fontSize: '0.8rem', marginTop: '1rem' }}>
              <span style={{ opacity: '0.6' }}>
                Active until:&nbsp;
              </span>
              <span>
                {formattedEndDate}
              </span>
            </div>
          }
        </div>
      }

      {status === "Closed" &&
        <div>
          <div className="pill" style={{ backgroundColor: '#ffffff40', color: textColor }}>
            Closed
          </div>
          {withTime &&
            <div style={{ fontSize: '0.8rem', marginTop: '1rem' }}>
              <span style={{ opacity: '0.6' }}>
                Ended at:&nbsp;
              </span>
              <span>
                {formattedEndDate}
              </span>
            </div>
          }
        </div>
      }
    </>
  )
}
