import { format, toDate } from 'date-fns'
import React from 'react'

export default function TimeLabel({ status, startTime, endTime, isUnix = true }) {
  let startDate;
  let endDate;
  let formattedStartDate;
  let formattedEndDate;

  if(isUnix){
    startDate = toDate(startTime * 1000)
    endDate = toDate(endTime * 1000)
    formattedStartDate = format(startDate, 'dd MMMM yyyy - mm:ss')
    formattedEndDate = format(endDate, 'dd MMMM yyyy - mm:ss')
  }else{
    formattedStartDate = startTime
    formattedEndDate = endTime
  }

  return (
    <>
      {status === "Scheduled" &&
        `Start at: ${formattedStartDate}`
      }

      {status === "Active" &&
        `Active until: ${formattedEndDate}`
      }

      {status === "Closed" &&
        `Ended at: ${formattedEndDate}`
      }
    </>
  )
}
