export const formatStatusAndTime = (data) => {
  const nowUnix = Math.floor(Date.now() / 1000);
  let status = "" // "COMING SOON" | "ACTIVE" | "ENDED
  if (nowUnix < data.startAt) status = "Scheduled"
  else if (nowUnix >= data.startAt && nowUnix <= data.endAt) status = "Active"
  else if (nowUnix > data.endAt) status = "Closed"

  const startDate = toDate(data.startAt * 1000)
  const endDate = toDate(data.endAt * 1000)

  const formattedStartDate = format(startDate, 'dd MMMM yyyy - hh:mm:ss')
  const formattedEndDate = format(endDate, 'dd MMMM yyyy - hh:mm:ss')

  return {
    ...data,
    status,
    formattedStartDate,
    formattedEndDate
  }
}